<template>
	<view class="content">
		<view class="cu-form-group">
			<view class="title">商品名称</view>
			<input maxlength="60" class="text-right" v-model="details.name" placeholder-class="placeholder"></input>
		</view>
		<view class="cu-form-group">
			<view class="title">货号</view>
			<input maxlength="50" class="text-right" v-model="details.number" placeholder-class="placeholder"></input>
		</view>
		<view class="cu-form-group">
			<view class="title">主图</view>
			<image v-if="details.imgArr.img" @click="mainChooseImage" class="cu-avatar radius bg-gray" :src="details.imgArr.img"/>
			<view v-else class="cu-avatar radius bg-gray" @click="mainChooseImage"></view>
		</view>
		<view class="cu-form-group">
			<text class="tit">商品分类</text>
			<picker mode="multiSelector" @change="MultiChange" @columnchange="MultiColumnChange" :value="multiIndex" :range="multiArray">
				<view class="picker">
					{{multiArray[0][multiIndex[0]]}}，{{multiArray[1][multiIndex[1]]}}，{{multiArray[2][multiIndex[2]]}}
				</view>
			</picker>
		</view>
		<view class="cu-bar bg-white margin-top-sm">
			<view class="action">
				图片列表
			</view>
			<view class="action">
				{{details.imgList.length}}/5
			</view>
		</view>
		<view class="cu-form-group">
			<view class="grid col-4 grid-square flex-sub">
				<view class="bg-img" v-for="(item,index) in details.imgList" :key="index" @tap="ViewImage" :data-url="item.response">
				 <image :src="item.response" mode="aspectFill"></image>
					<view class="cu-tag bg-red" @tap.stop="DelImg" :data-index="index">
						<text class='cuIcon-close'></text>
					</view>
				</view>
				<view class="solids" @tap="ChooseImage" v-if="details.imgList.length<5">
					<text class='cuIcon-cameraadd'></text>
				</view>
			</view>
		</view>
		<view class="cu-list menu sm-border">
			<view @click="goSpecification" class="cu-item arrow">
				<view class="content">
					<text>商品规格</text>
				</view>
			</view>
			<view @click="goEditor" class="cu-item arrow">
				<view class="content">
					<text>商品详情</text>
				</view>
			</view>
			<view class="cu-form-group">
				<view class="title">立即上架</view>
				<switch @change="setShow" :class="details.is_show?'checked':''" :checked="details.is_show?true:false"></switch>
			</view>
			<view class="cu-form-group">
				<view class="title">排序</view>
				<input maxlength="11" class="text-right" v-model="details.sort" placeholder-class="placeholder"></input>
			</view>
		</view>
		<button class="add-btn" @click="confirm">提交</button>
	</view>
</template>

<script>
	import Good from '../../api/good'
	import { objectURLToBlob } from '../../utils'
	export default {
		data() {
			return {
				multiArray: [
					[],
					[],
					[]
				],
				id: '',
				oid: '',
				details: {
					imgList: [],
					imgArr: {
						img: ''
					},
					good_sku:[],
					details: '',
					is_show: 1,
					sort: 5,
				},
				list: [],
				multiIndex: [0, 0, 0],
			}
		},
		onLoad(option){
			this.oid = option.oid
			this.details.good_indent_id = this.oid
			this.id = option.id
			this.index = option.index
			this.selectGoodCategory()
			this.getList()
		},
		methods: {
			async getList(){
				let title = '新增商品'
				const that = this
				if(this.id > 0){
					let multiArray = [
						this.multiArray[0],
						[],
						[]
					]
					title = '编辑商品'
					await Good.detailsSubmit(this.id,{},function(res){
						that.details = res.goods
						if(res.category.length>0){
							for (const i in that.list) {
								const two = that.list[i]
								if(two.id === res.category[0]){
									that.multiIndex[0] = i
									
									if(two.children.length>0){
										for(const j in two.children){
											multiArray[1].push(two.children[j].name)
											if(two.children[j].id === res.category[1]){
												that.multiIndex[1] = j
												const three = two.children[j].children
												if(three.length>0){
													for(const k in three){
														multiArray[2].push(three[k].name)
														if(three[k].id === res.category[2]){
															that.multiIndex[2] = k
															
														}
													}
												}
											}
										}
									}
								}
							}
							that.multiArray = multiArray
						}
					})
					
				}
				uni.setNavigationBarTitle({
					title
				})
			},
			async selectGoodCategory(){
				let multiArray = [
					[],
					[],
					[]
				]
				
				const that = this
				await Good.selectGoodCategory({},function(res){
					that.list = res
					res.forEach((item)=>{
						that.multiArray[0].push(item.name)
					})
					that.getCategory()
				})
			},
			//获取当前可选分类
			getCategory(){
				const that = this
				const list = this.list
				const multiIndex = this.multiIndex
				let multiArray = [
					this.multiArray[0],
					[],
					[]
				]
				// 取第二列
				multiArray[1] = []
				const two = list[multiIndex[0]]['children']
				if(two){
					for (const i in two) {
						multiArray[1].push(two[i].name)
					}
					
				}else{
					that.$api.msg('商品分类配置有误');
					return false
				}
				// 取第三列
				multiArray[2] = []
				const three = list[multiIndex[0]]['children'][multiIndex[1]]['children']
				if(three){
					for (const i in three) {
						multiArray[2].push(three[i].name)
					}
					
				}else{
					that.$api.msg('商品分类配置有误');
					return false
				}
				this.multiArray = multiArray
			},
			MultiColumnChange(e) {
				let data = {
					multiArray: this.multiArray,
					multiIndex: this.multiIndex
				};
				this.multiIndex[e.detail.column] = e.detail.value
				this.getCategory()
			},
			//提交
			confirm(){
				let data = this.details
				const multiIndex = this.multiIndex
				data.category =this.list[multiIndex[0]]['children'][multiIndex[1]]['children'][multiIndex[2]].id
				const that = this
				if(!data.name){
					this.$api.msg('请填写商品');
					return;
				}
				if(!data.number){
					this.$api.msg('请填写商品货号');
					return;
				}
				if(!data.imgArr.img){
					this.$api.msg('请上传主图');
					return;
				}
				if(data.good_sku.length === 0){
					this.$api.msg('请配置商品规格');
					return;
				}
				if(data.id){
					Good.updateSubmit(data,function(res){
						that.$api.prePage().refreshGoods()
						that.$api.msg('商品保存成功')
						setTimeout(()=>{
							uni.navigateBack()
						}, 800)
					})
				}else{
					Good.createSubmit(data,function(res){
						that.$api.prePage().refreshGoods()
						that.$api.msg('商品添加成功')
						setTimeout(()=>{
							uni.navigateBack()
						}, 800)
					})
				}
			},
			//商品SKU列表
			refreshGoodSkuList(data){
				this.details.good_sku = data
			},
			//商品详情
			refreshDetails(data){
				this.details.details = data
			},
			//主图
			mainChooseImage() {
				let that = this
				let uploadFile = ''
				uni.chooseImage({
					count: 1,
					sizeType: ['compressed'],
					success: (res) => {
						uni.uploadFile({
						  url: uni.getStorageSync('applyGoodSecret').host + 'uploadPictures',
						  filePath: res.tempFilePaths[0],
						  name: 'file',
						  header: {
							'apply-secret': uni.getStorageSync('applyGoodSecret').secret,
							'Authorization': 'Bearer ' + uni.getStorageSync('applytoken')
						  },
						  formData: {
							type: 1,
							size: 1024 * 500
						  },
						  success(res) {
							that.details.imgArr.img =res.data
						  },
						  fail(res) {
							that.$api.msg(res.message);
							return false
						  }
						})
					}
				});
			},
			//图片列表
			ChooseImage() {
				let that = this
				let uploadFile = ''
				uni.chooseImage({
					count: 5,
					sizeType: ['compressed'],
					success: (res) => {
						for(var n = 0; n < res.tempFilePaths.length; n++) {
							uni.uploadFile({
							  url: uni.getStorageSync('applyGoodSecret').host + 'uploadPictures',
							  filePath: res.tempFilePaths[n],
							  name: 'file',
							  header: {
								'apply-secret': uni.getStorageSync('applyGoodSecret').secret,
								'Authorization': 'Bearer ' + uni.getStorageSync('applytoken')
							  },
							  formData: {
								type: 1,
								size: 1024 * 500
							  },
							  success(res) {
								that.details.imgList.push({
									response: res.data,
									url: res.data
								})
							  },
							  fail(res) {
								that.$api.msg(res.message);
								return false
							  }
							})
						}
					}
				});
			},
			ViewImage(e) {
				let imgList = []
				this.details.imgList.forEach(item=>{
					imgList.push(item.url)
				})
				uni.previewImage({
					urls: imgList,
					current: e.currentTarget.dataset.url
				});
			},
			DelImg(e) {
				uni.showModal({
					title: '提示',
					content: '确定要删除该图片吗？',
					cancelText: '再看看',
					confirmText: '确实',
					success: res => {
						if (res.confirm) {
							this.details.imgList.splice(e.currentTarget.dataset.index, 1)
						}
					}
				})
			},
			goodsAllocated(){
				uni.navigateTo({
					url: `/pages/order/goods_allocated?oid=${this.oid}`
				})
			},
			goSpecification(){
				const data = this.details.good_sku.length>0 ? JSON.stringify(this.details.good_sku) : ''
				uni.navigateTo({
					url: `/pages/store/specification?data=${data}`
				})
			},
			goEditor(){
				const data = this.details.details ? encodeURIComponent(this.details.details) : ''
				uni.navigateTo({
					url: `/pages/store/editor?data=${data}`
				})
			},
			MultiChange(e) {
				this.multiIndex = e.detail.value
			},
			setShow(e){
				this.details.is_show = e.detail.value
			}
		}
	}
</script>

<style lang="scss">
	page{
		background: $page-color-base;
		padding-top: 16upx;
	}

	.row{
		display: flex;
		align-items: center;
		position: relative;
		padding:0 30upx;
		height: 110upx;
		background: #fff;
		
		.tit{
			flex-shrink: 0;
			width: 120upx;
			font-size: 30upx;
			color: $font-color-dark;
		}
		.input{
			flex: 1;
			font-size: 30upx;
			color: $font-color-dark;
		}
		.icon-shouhuodizhi{
			font-size: 36upx;
			color: $font-color-light;
		}
	}
	.default-row{
		margin-top: 16upx;
		.tit{
			flex: 1;
		}
		switch{
			transform: translateX(16upx) scale(.9);
		}
	}
	.add-btn{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 690upx;
		height: 80upx;
		margin: 60upx auto;
		font-size: $font-lg;
		color: #fff;
		background-color: $base-color;
		border-radius: 10upx;
		box-shadow: 1px 2px 5px rgba(219, 63, 96, 0.4);
	}
	.goods-section {
		background: #fff;
		padding-bottom: 1px;
	
		.g-header {
			display: flex;
			align-items: center;
			height: 84upx;
			padding: 0 30upx;
			position: relative;
		}
	
		.logo {
			display: block;
			width: 50upx;
			height: 50upx;
			border-radius: 100px;
		}
	
		.name {
			font-size: 30upx;
			color: $font-color-base;
			margin-left: 24upx;
		}
	
		.g-item {
			display: flex;
			margin: 20upx 30upx;
	
			image {
				flex-shrink: 0;
				display: block;
				width: 140upx;
				height: 140upx;
				border-radius: 4upx;
			}
	
			.right {
				flex: 1;
				padding-left: 24upx;
				overflow: hidden;
			}
	
			.title {
				font-size: 30upx;
				color: $font-color-dark;
			}
	
			.spec {
				font-size: 26upx;
				color: $font-color-light;
			}
	
			.price-box {
				display: flex;
				align-items: center;
				font-size: 32upx;
				color: $font-color-dark;
				padding-top: 10upx;
	
				.price {
					margin-bottom: 4upx;
				}
				.number{
					font-size: 26upx;
					color: $font-color-base;
					margin-left: 20upx;
				}
				.delete-button{
					position:absolute;
					right:20upx;
				}
			}
			.text-icon{
				font-size: 40upx;
			}
			.step-box {
				position: relative;
			}
		}
	}
</style>
